<script>
import $ from 'jquery';

export default {
  mounted() {
    // searchTop
    $('.searchTop input, .searchTop button').hide();
    const $win = $(window);
    const $searchTop = $('.searchTop input, .searchTop button');
    $win.scroll(() => {
